.toaster {
  padding: 40px 20px;
  background-color: #111;
  opacity: 0.8;
  color: #fff;
  border-radius: 3px;
  margin: 5px;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
  -webkit-animation: comein 200ms ease-in-out 1 forwards;
          animation: comein 200ms ease-in-out 1 forwards;
}

.toaster-remove {
  composes: toaster;
  -webkit-animation-name: remove;
          animation-name: remove;
}

.toaster:hover {
  opacity: 1;
}

.toaster-info {
  padding-right: 15px;
  font-size: 1.5rem;
  line-height: 0.7;
}

.toaster-message {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.toaster-close {
  /* reset button */
  background-color: transparent;
  color: inherit;
  padding: 0;
  margin: 0;
  border: none;
  /**************/
  padding-left: 15px;
  font-size: 2rem;
  margin-left: auto;
  line-height: 0.5;
  cursor: pointer;
}

.toaster-close:focus {
  outline: none;
}

.toaster-close:hover {
  color: red;
}

@-webkit-keyframes comein {
  from {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes comein {
  from {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes remove {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

@keyframes remove {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}



